body {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  transition: background-color .3s ease-in-out;
}

h1, h2, h3 {
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

.result {
  margin-bottom: 3rem;
}

#highscore {
  flex-flow: column;
  align-items: center;
  display: flex;
}

.score-div {
  flex-flow: row;
  justify-content: space-between;
  width: 11rem;
  padding: 0 1rem;
  display: flex;
  box-shadow: 0 1px 2px #3c40434d, 0 2px 6px 2px #3c404326;
}

.error {
  border: 3px solid #9800009c;
  border-radius: 4px;
  width: 15rem;
  padding: 2rem;
}

.correct {
  background-color: #2cb276;
}

.wrong {
  background-color: #d36060;
}
